import {
    REQUEST_ALL_MEMBERS,
    REQUEST_ALL_MEMBERS_FAILURE,
    REQUEST_ALL_MEMBERS_SUCCESS
} from "../../constant/memberConstant";

const initialState = {
    members: {
        data: [],
        status: 'idle',
        isLoading: false,
    },
};

export const membersReducer = (state = initialState, action: any) => {
    const {type, payload} = action;
    switch (type) {
        case REQUEST_ALL_MEMBERS:
            return {
                ...state,
                members: {
                    data: [],
                    status: 'fetching',
                    isLoading: true,
                },
            };
        case REQUEST_ALL_MEMBERS_SUCCESS:
            return {
                ...state,
                members: {
                    data: payload,
                    status: 'ready',
                    isLoading: false,
                },
            };
        case REQUEST_ALL_MEMBERS_FAILURE:
            return {
                ...state,
                members: {
                    data: [],
                    status: 'error',
                    isLoading: false,
                },
            };
        default:
            return state;
    }
};
