import {
    REQUEST_MEMBER_REGISTRATION_DETAIL,
    REQUEST_MEMBER_REGISTRATION_DETAIL_FAILURE,
    REQUEST_MEMBER_REGISTRATION_DETAIL_SUCCESS
} from "../../constant/memberConstant";

const initialState = {
    memberDetail: {
        data: [],
        status: 'idle',
        isLoading: true,
    },
};

export const memberDetailReducer = (state = initialState, action) => {
    const { type, payload } = action;
    switch (type) {
        case REQUEST_MEMBER_REGISTRATION_DETAIL:
            return {
                ...state,
                memberDetail: {
                    data: [],
                    status: 'fetching',
                    isLoading: true,
                },
            };
        case REQUEST_MEMBER_REGISTRATION_DETAIL_SUCCESS:
            return {
                ...state,
                memberDetail: {
                    data: payload,
                    status: 'ready',
                    isLoading: false,
                },
            };
        case REQUEST_MEMBER_REGISTRATION_DETAIL_FAILURE:
            return {
                ...state,
                memberDetail: {
                    data: [],
                    status: 'fetching',
                    isLoading: false,
                },
            };
        default:
            return state;
    }
};
