import {
    REQUEST_ORDER_JERSEY,
    REQUEST_ORDER_JERSEY_FAILURE,
    REQUEST_ORDER_JERSEY_SUCCESS
} from "../../constant/memberConstant";

const initialState = {
    orderJersey: {
        data: [],
        status: 'idle',
        isLoading: true,
    },
};

export const orderJerseyReducer = (state = initialState, action: any) => {
    const {type, payload} = action;
    switch (type) {
        case REQUEST_ORDER_JERSEY:
            return {
                ...state,
                orderJersey: {
                    data: [],
                    status: 'fetching',
                    isLoading: true,
                },
            };
        case REQUEST_ORDER_JERSEY_SUCCESS:
            return {
                ...state,
                orderJersey: {
                    data: payload,
                    status: 'ready',
                    isLoading: false,
                },
            };
        case REQUEST_ORDER_JERSEY_FAILURE:
            return {
                ...state,
                orderJersey: {
                    data: [],
                    status: 'fetching',
                    isLoading: false,
                },
            };
        default:
            return state;
    }
};
