import {REQUEST_REGISTRATION_EVENT,REQUEST_REGISTRATION_EVENT_SUCCESS, REQUEST_REGISTRATION_EVENT_FAILURE} from "../../constant/EventConstant";

const initialState = {
  eventRegistrationList: {
    data: [],
    status: 'idle',
    isLoading: true,
  },
};

export const eventRegistrationListReducer = (state = initialState, action) => {
  const { type, payload } = action;
  switch (type) {
    case REQUEST_REGISTRATION_EVENT:
      return {
        ...state,
        eventRegistrationList: {
          data: [],
          status: 'fetching',
          isLoading: true,
        },
      };
    case REQUEST_REGISTRATION_EVENT_SUCCESS:
      return {
        ...state,
        eventRegistrationList: {
          data: payload,
          status: 'ready',
          isLoading: false,
        },
      };
    case REQUEST_REGISTRATION_EVENT_FAILURE:
      return {
        ...state,
        eventRegistrationList: {
          data: [],
          status: 'fetching',
          isLoading: false,
        },
      };
    default:
      return state;
  }
}
