import { REQUEST_PARENT, REQUEST_PARENT_FAILED, REQUEST_PARENT_SUCCESS } from "../constant/UsersConstant";

const initialState = {
    parent: {
        data: [],
        status: 'idle',
        isLoading: true,
    },
};
export const parentReducer = (state = initialState, action) => {
    const { type, payload } = action;
    switch (type) {
        case REQUEST_PARENT:
            return {
                ...state,
                parent: {
                    data: [],
                    status: 'fetching',
                    isLoading: true,
                },
            };
        case REQUEST_PARENT_SUCCESS:
            return {
                ...state,
                parent: {
                    data: payload,
                    status: 'ready',
                    isLoading: false,
                },
            };
        case REQUEST_PARENT_FAILED:
            return {
                ...state,
                parent: {
                    data: [],
                    status: 'fetching',
                    isLoading: false,
                },
            };
        default:
            return state;
    }
}
