import {
    REQUEST_MEMBER_REGISTRATION,
    REQUEST_MEMBER_REGISTRATION_FAILURE,
    REQUEST_MEMBER_REGISTRATION_SUCCESS
} from "../../constant/memberConstant";

const initialState = {
    memberRegistration: {
        data: [],
        status: 'idle',
        isLoading: true,
    },
};

export const memberRegistrationReducer = (state = initialState, action: any) => {
    const {type, payload} = action;
    switch (type) {
        case REQUEST_MEMBER_REGISTRATION:
            return {
                ...state,
                memberRegistration: {
                    data: [],
                    status: 'fetching',
                    isLoading: true,
                },
            };
        case REQUEST_MEMBER_REGISTRATION_SUCCESS:
            return {
                ...state,
                memberRegistration: {
                    data: payload,
                    status: 'ready',
                    isLoading: false,
                },
            };
        case REQUEST_MEMBER_REGISTRATION_FAILURE:
            return {
                ...state,
                memberRegistration: {
                    data: [],
                    status: 'fetching',
                    isLoading: false,
                },
            };
        default:
            return state;
    }
};
