import {
  REQUEST_EVENT_ACTIVE,
  REQUEST_EVENT_ACTIVE_FAILED,
  REQUEST_EVENT_ACTIVE_SUCCESS,
} from '../../constant/EventConstant';

const initialState = {
  eventActive: {
    data: [],
    status: 'idle',
    isLoading: true,
  },
};

export const eventActiveReducer = (state = initialState, action: any) => {
  const {type, payload} = action;

  switch (type) {
    case REQUEST_EVENT_ACTIVE:
      return {
        ...state,
        eventActive: {
          data: [],
          status: 'fetching',
          isLoading: true,
        },
      };
    case REQUEST_EVENT_ACTIVE_SUCCESS:
      return {
        ...state,
        eventActive: {
          data: payload,
          status: 'ready',
          isLoading: false,
        },
      };
    case REQUEST_EVENT_ACTIVE_FAILED:
      return {
        ...state,
        eventActive: {
          data: [],
          status: 'fetching',
          isLoading: false,
        },
      };
    default:
      return state;
  }
};
