import {
    REQUEST_MASTER_CATEGORY_RACE,
    REQUEST_MASTER_CATEGORY_RACE_FAILED,
    REQUEST_MASTER_CATEGORY_RACE_SUCCESS
} from "../constant/MasterConstant";

const initialState = {
    masterCategoryRace: {
        data: [],
        status: 'idle',
        isLoading: true,
    },
}

const masterCategoryRaceReducer = (state = initialState, action) => {
    const { type, payload } = action;

    switch (type) {
        case REQUEST_MASTER_CATEGORY_RACE:
            return {
                ...state,
                masterCategoryRace: {
                    data: [],
                    status: 'fetching',
                    isLoading: true,
                },
            };
        case REQUEST_MASTER_CATEGORY_RACE_SUCCESS:
            return {
                ...state,
                masterCategoryRace: {
                    data: payload,
                    status: 'ready',
                    isLoading: false,
                },
            };
        case REQUEST_MASTER_CATEGORY_RACE_FAILED:
            return {
                ...state,
                masterCategoryRace: {
                    data: [],
                    status: 'failed',
                    isLoading: true,
                },
            };
        default:
            return state;
    }
}


export default masterCategoryRaceReducer;
