import {
    REQUEST_ALL_MEMBERS_BY_USER_ID,
    REQUEST_ALL_MEMBERS_BY_USER_ID_FAILURE,
    REQUEST_ALL_MEMBERS_BY_USER_ID_SUCCESS
} from '../../constant/MemberConstatant'

const initialState = {
    membersByUserId: {
        data: [],
        status: 'idle',
        isLoading: true,
    },
};

export const memberByUserIdReducer = (state = initialState, action) => {
    const { type, payload } = action;

    switch (type) {
        case REQUEST_ALL_MEMBERS_BY_USER_ID:
            return {
                ...state,
                membersByUserId: {
                    data: [],
                    status: 'fetching',
                    isLoading: true,
                },
            };
        case REQUEST_ALL_MEMBERS_BY_USER_ID_SUCCESS:
            return {
                ...state,
                membersByUserId: {
                    data: payload,
                    status: 'ready',
                    isLoading: false,
                },
            };
        case REQUEST_ALL_MEMBERS_BY_USER_ID_FAILURE:
            return {
                ...state,
                membersByUserId: {
                    data: [],
                    status: 'fetching',
                    isLoading: false,
                },
            };
        default:
            return state;
    }
};
