import {REQUEST_LOGIN, REQUEST_LOGIN_FAILED, REQUEST_LOGIN_SUCCESS} from "../constant/authConstant";

const initialState = {
    login: {
        data: [],
        status: 'idle',
        isLoading: true,
    },
};

export const loginReducer = (state = initialState, action: any) => {
    const {type, payload} = action;
    switch (type) {
        case REQUEST_LOGIN:
            return {
                ...state,
                login: {
                    data: [],
                    status: 'fetching',
                    isLoading: true,
                },
            };
        case REQUEST_LOGIN_SUCCESS:
            return {
                ...state,
                login: {
                    data: payload,
                    status: 'ready',
                    isLoading: false,
                },
            };
        case REQUEST_LOGIN_FAILED:
            return {
                ...state,
                login: {
                    data: [],
                    status: 'fetching',
                    isLoading: false,
                },
            };
        default:
            return state;
    }
};
